const KeyCodeParser = (() => {
    const KEY_CODES = {
        RIGHT: [39, 403],    // ArrowRight, ColorF0Red
        LEFT: [37, 404],     // ArrowLeft, ColorF1Green
        UP: [38, 400],       // ArrowUp, ColorF2Yellow
        DOWN: [40, 401],     // ArrowDown, ColorF3Blue
        ENTER: [13, 29443],  // Enter, Select
        BACK: [27, 10009, 461, 8] // Escape, Back, Back, Backspace
    };

    const KEYS = {
        RIGHT: ["ArrowRight", "Right"],
        LEFT: ["ArrowLeft", "Left"],
        UP: ["ArrowUp", "Up"],
        DOWN: ["ArrowDown", "Down"],
        ENTER: ["Enter", "Select"],
        BACK: ["Escape", "back", "GoBack"],
    };

    const isUp = event => KEY_CODES.UP.includes(event.keyCode) || KEYS.UP.includes(event.key);
    const isDown = event => KEY_CODES.DOWN.includes(event.keyCode) || KEYS.DOWN.includes(event.key);
    const isRight = event => KEY_CODES.RIGHT.includes(event.keyCode) || KEYS.RIGHT.includes(event.key);
    const isLeft = event => KEY_CODES.LEFT.includes(event.keyCode) || KEYS.LEFT.includes(event.key);
    const isEnter = event => KEY_CODES.ENTER.includes(event.keyCode) || KEYS.ENTER.includes(event.key);
    const isBack = event => KEY_CODES.BACK.includes(event.keyCode) || KEYS.BACK.includes(event.key);

    const normalize = (event) => {
        const keyCode = event.keyCode || event.which;
        const key = event.key;
        return { keyCode, key };
    };

    const parseEvent = (evt) => {
        const event = normalize(evt);
        let UP = false, DOWN = false, LEFT = false;
        let RIGHT = false, ENTER = false, BACK = false;
        if (UP = isUp(event))
            UP = true;
        else if (DOWN = isDown(event))
            DOWN = true;
        else if (LEFT = isLeft(event))
            LEFT = true;
        else if (RIGHT = isRight(event))
            RIGHT = true;
        else if (ENTER = isEnter(event))
            ENTER = true;
        else if (BACK = isBack(event))
            BACK = true;
        return { UP, DOWN, LEFT, RIGHT, ENTER, BACK };
    };

    // Export the methods for ES module usage
    return { parseEvent };
})();

// Add this for compatibility with both module systems
if (typeof window !== "undefined") {
    window.KeyCodeParser = KeyCodeParser;
}

export default KeyCodeParser;